  display: flex;
  justify-content: center;
}

.navbar a {
  color: white;
  padding: 2rem;
  text-decoration: none;
  font-size: 1rem;
  font-family: "Amatic Sc", cursive;
}

.navbar a:hover {
  background-color: #ace0f9;
